import { useEffect, useState } from 'react'

const useHolderData = () => {
    const [data, setData] = useState(null);

    const [bothData, setBothData] = useState(null);
    const [stakingData, setStakingData] = useState(null);
    const [circulatingData, setCirculatingData] = useState(null);

    const getEcosystemInfo = () => {
        return new Promise((resolve, reject) => {
            fetch(`ecosystem/holders/all`).then(result => {
                result.json().then(res => {
                    setCirculatingData(res);
                    resolve(res);
                })
            })
            .catch(err => reject(err))
        })
    }

    const getStakingEcosystemInfo = () => {
        return new Promise((resolve, reject) => {
            fetch(`staking/ecosystem`).then(result => {
                result.json().then(res => {
                    setStakingData(res);
                    resolve(res);
                })
            })
            .catch(err => reject(err))
        })
    }

    useEffect(() => {
        Promise.all([getEcosystemInfo(), getStakingEcosystemInfo()]).then(data => { combineData(data) })
    }, [])

    const combineData = data => {
        let combinedData = {
            "shrimp": { count: 0, totalAxn: 0 },
            "crab": { count: 0, totalAxn: 0 },
            "fish": { count: 0, totalAxn: 0 },
            "octopus": { count: 0, totalAxn: 0 },
            "dolphin": { count: 0, totalAxn: 0 },
            "tigerShark": { count: 0, totalAxn: 0 },
            "greatWhite": { count: 0, totalAxn: 0 },
            "whale": { count: 0, totalAxn: 0 },
            "totals": { held_axn: 0, holders: 0, last_updated: 0 }
        }

        Object.keys(combinedData).map(cd => {
            if (cd === "totals") {
                combinedData[cd].held_axn = (data[0][cd].held_axn + data[1][cd].held_axn)
                combinedData[cd].holders = (data[0][cd].holders + data[1][cd].holders)
                combinedData[cd].last_updated = (data[0][cd].last_updated + data[1][cd].last_updated)
            } else {
                combinedData[cd].count = (data[0][cd].count + data[1][cd].count)
                combinedData[cd].totalAxn = (data[0][cd].totalAxn + data[1][cd].totalAxn)
            }
        })
        setData(combinedData)
        setBothData(combinedData)
    }

    const changeData = selected => {
        if(selected.liquid && selected.staked)
            setData(bothData)
        else if (selected.liquid)
            setData(circulatingData)
        else if (selected.staked)
            setData(stakingData)
        else 
            setData({
                "shrimp": { count: 0, totalAxn: 0 },
                "crab": { count: 0, totalAxn: 0 },
                "fish": { count: 0, totalAxn: 0 },
                "octopus": { count: 0, totalAxn: 0 },
                "dolphin": { count: 0, totalAxn: 0 },
                "tigerShark": { count: 0, totalAxn: 0 },
                "greatWhite": { count: 0, totalAxn: 0 },
                "whale": { count: 0, totalAxn: 0 },
                "totals": { held_axn: 0, holders: 0, last_updated: 0 }
            })
    }

    return {
        data,
        changeData
    }
}


export default useHolderData;